* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html {
    font-size: 62.5%;
}
*:focus {
    outline: none;
    box-shadow: 0 0 0 0.5rem #f72585;
    transform: scale(1.1);
    transition: all 0.2s ease 0s;
}
.section--hidden {
    opacity: 0;
    transform: translateY(8rem);
    transition: all ease 1s 1s;
}
.section--visible {
    opacity: 1;
    transform: translateY(0rem);
    transition: all ease 1s 1s;
}
.preLoad {
    display: none;
}
